<template>
  <div id="handRecord-scroll">
    <div
      style="margin-top:10px;"
      v-if="recordList.length > 0"
    >
      <div
        v-for="(item,i) in recordList"
        :key="i"
      >
        <div class="record">
          <div class="left">
            <div>{{ $route.query.type == '1'?'交账':'充值' }}-{{ paymentObj[item.payment] }}</div>
            <div class="c_89">{{ item.handTime | dataFilter('YYYY年MM月DD日') }}</div>
          </div>
          <div class="right">
            <div class="c_f60">&yen;{{ Number(item.handAmount).toFixed(2) }}</div>
            <div class="c_89">{{ item.sysUser.realName || item.sysUser.nickName }}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { GetBusinessAccountRecordByhotel } from '@/api/api_business_hotel_and_scale';
import { GetSystemDictDataByType } from '@/api/api_query';
export default {
    data() {
        return {
            recordList: [],
            paymentObj: {}
        };
    },
    created() {
        this.$store.commit('setHeader', `${this.$route.query.type}` == '1' ? '交账记录' : '充值记录');
        this.getRecord();
    },
    methods: {
        // 根据酒店和类型查询交账记录
        getRecord() {
            GetBusinessAccountRecordByhotel(this.$route.query.hotelId, { accountType: this.$route.query.type }).done((res) => {
                this.recordList = res.data;
                this.getPayment();
            });
        },
        // 查询字典-支付方式（线下）
        getPayment() {
            GetSystemDictDataByType({ dictType: 'business_payment_offline' }).done((res) => {
                this.paymentObj = {};
                res.data.map((item) => (this.paymentObj[item.dictValue] = item.dictLabel));
            });
        }
    }
};
</script>
<style scoped>
#handRecord-scroll {
    position: fixed;
    top: 65px;
    bottom: 0;
    left: 0;
    right: 0;
    overflow-y: scroll;
    background: #fff;
}
.record {
    background: #fff;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #f4f4f4;
    margin: 4px 12px;
    padding: 6px 4px;
    line-height: 28px;
    font-size: 15px;
    color: #000;
}
.left {
    text-align: left;
}
.right {
    text-align: right;
}
.c_f60 {
    color: #ff7d41;
}
.c_89 {
    color: #a2a2a2;
    font-size: 13px;
}
</style>



